
import Search from 'vux/src/components/search/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'

import {managerPersonal, managerSearch, managerLower} from '@/api/manager-personal';//
import baseLoading from 'components/base-loading.vue';

export default {
name: 'manager-personal',
components: {
Search,
Group,
Cell,
AlertPlugin,
baseLoading,
},
data() {
return {
isLoadingData: true,
showContent: true,//
activeLevel: 1,
managerPersonList: [],
selfName: "",//
selfLevel: "",//
showNameFlag: false,
searchText: "",//,
searchResult: false,//
searchList: "",
leftList: [],//
juniorDetail: "",
maxLevel: "",
isShowButtom: false,
start: "",
page: 1,
isBottom: false,
}
},
computed: {
searchData: function () {//
let searchText = this.searchText;
if (searchText) {
return this.searchList.filter(function (people) {
return Object.keys(people).some(function (key) {
return String(people[key]).toLowerCase().indexOf(searchText) > -1
})
})
}
return this.searchList;
},
showRank: function () {
for (let rank = 0; rank < this.maxLevel; rank++) {
this.leftList.push({showName: ""});
}
return this.leftList;
}
},
methods: {
choseItem(item, index){//
this.page = 1;
this.managerPersonList = [];
this.activeLevel = index + 1;//

let indexFlag = index + 1;

for (let rank = 1; rank <= this.maxLevel; rank++) {
for (let low = index; low < this.maxLevel; low++) {
this.leftList[low].showName = "";
}
if (rank === indexFlag) {
this.isBottom = false;
this.getList('1', rank);
}
}
},
setEmpty(){//  
this.isLoadingData = true;
this.page = 1;
this.managerPersonList = [];
this.activeLevel = 1;
this.getList("1", this.activeLevel, "");
for (let j = 0; j < this.leftList.length; j++) {//
this.leftList[j].showName = "";
}
},
showResult(){//
this.showContent = false;//
this.searchResult = true;//
},
revertContent(){//
this.showContent = true;
this.searchResult = false;
},


showLower(item){
this.isLoadingData = true;
this.managerPersonList = [];
this.page = 1;
this.showContent = true;
this.searchResult = false;
this.isBottom = false;

this.selfName = item.hrEmpName;//
this.selfLevel = parseInt(item.hrEmpLevel);

this.showNameFlag = (this.activeLevel === this.selfLevel);
if (this.showContent === false) {
this.leftList[this.selfLevel - 1].showName = item.hrEmpName;
}
if (item.hrEmpLevel <= this.maxLevel) {
this.getList("1", item.hrEmpLevel + 1, item.hrEmpName);//
this.activeLevel += 1;
if (this.selfLevel === this.maxLevel) {
this.getList("1", item.hrEmpLevel, item.hrEmpName);//
this.$vux.alert.show({
content: '',
});
}
}
for (let j = 0; j < this.leftList.length; j++) {//
if (this.selfLevel === (j + 1)) {
this.leftList[j].showName = this.selfName;
}
}
},
onScrollBottom(done) {
if (this.isBottom) {

setTimeout(() => {
done(true)
}, 500);
} else {
this.page++;
this.getList(this.page, this.activeLevel, this.selfName, done);
console.log("" + this.page + "." + "" + this.activeLevel + "");
}
},
getList(page, activeLevel, selfName, done){
managerPersonal({//
"page": page,
"pagesize": "10",
"hrEmpLevel": activeLevel,
"hrEmpName": selfName
},).then(response => {
if (response.data && response.data.rows) {
for (let i = 0; i < response.data.rows.length; i++) {
this.managerPersonList.push(response.data.rows[i]);
}

setTimeout(() => {
done && done()
}, 2000);
if (response.data.rows.length < 10) {
this.isBottom = true;
}
this.isLoadingData = false;
} else {
this.isLoadingData = false;
}
}).catch(error => {
this.isLoadingData = false;
});
},
goJuniorDetail(item){//
this.$emit('transferButtom', this.isShowButtom);
if (this.juniorDetail === "") {
this.$router.push({
name: 'payRollReport',
params: {"item": item.hrEmpId, "juniorDetail": this.juniorDetail}
});
} else if (this.juniorDetail === "") {
this.$router.push({
name: 'personal',
params: {"item": item.hrEmpId, "juniorDetail": this.juniorDetail}
});
}
},
},
mounted() {

this.getList("1", this.activeLevel, "");

managerSearch({
"page": "1",
"pagesize": "10",
"hrEmpName": this.searchText,
}).then(response => {
if (response.data && response.data.rows) {
this.searchList = response.data.rows;
this.isLoadingData = false;
} else {
this.isLoadingData = false;
}
}).catch(error => {
this.isLoadingData = false;
});

managerLower({"hrEmpId": "123"}).then(response => {
if (response.data && response.data.rows) {
this.isLoadingData = false;
for (let i = 0; i < response.data.rows.length; i++) {

this.maxLevel = parseInt(response.data.rows[i].hrEmpLowerLevel);
}
} else {
this.isLoadingData = false;
}
}).catch(error => {
this.isLoadingData = false;
});
}
}



